@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
* ::before::after {
  box-sizing: border-box;
  margin: 0;
}

.App {
  text-align: center;
  font-family: "Press Start 2P", cursive;
}

.rightSection {
  color: white;
  margin: 30px 0 0 0;
  font-size: 20px;
}

.login {
  margin-right: 4px;
}

iframe {
  margin-top: 50px;
  width: 600px;
  height: 600px;
}
.samplePacksLogo {
  margin-top: 29px;
}
.templets-logo {
  margin-left: 8px;
}
.paypal {
  margin: 20px auto 20px auto;
  width: 50%;
}
.after-buy {
  font-family: monospace;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid black;
}
.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 8px;
}
.myLogo {
  margin-right: 20px;
  margin-top: 5px;
}
@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .logo {
    width: 320px;
    margin: 0 20px 0 20px;
  }
  .link {
    margin-bottom: 20px;
  }
  .rightSection {
    color: white;
    margin-top: 0px;
    font-size: 20px;
    text-align: center;
  }
  .select {
    font-family: "Press Start 2P", cursive;
  }
}

